<template>
    <div class="Activity">
      <div class="ul-box">
        <div class="list clearfloat"  v-for="(car,index) in list" :key="index" @click="goPage(index)">
			<div class="left">
				<img :src="car.img">
			</div>
           	<div class="right">
				<div class="title">{{car.title}}</div>
				<div class="text">
				  {{car.text}}
				</div>
				<span>了解更多》</span>
			</div>
        </div>
      </div>
  </div>
</template>
<script>
  export default {
	data () {
    return {
       list:[
		   {
			   img:require('../assets/activitya/1-c.png'),
			   title:"许麟庐艺术馆在烟台市博物馆开馆——市民可免费参观",
			   text:"2015年10月30日讯，许麟庐艺术馆开馆仪式于10月29日上午在市博物馆一楼中央大厅举行,"
					+"许麟庐师生友情展也在许麟庐艺术馆同步展出。今后,烟台市民可免费在许麟庐艺术馆欣赏到大师的珍贵作品。"
					+"下午,烟台市博物馆还举办了“写意人生——许麟庐书画艺术”学术研讨会,就许麟庐先生书画艺术研究成果进行了深入探讨和交流。"
		   }
	   ]
    }
  },
    methods: {
        goPage(type){
             this.$router.push({path:'/ActivityDetails',query:{id:type}});
        }
	},
	mounted(){}
  }
</script>
<style lang="less" scoped>
.ul-box{
	margin:  0 auto;
	width: 1170px;
	.left{
		width: 60%;
		height: 300px;
		float: left ;
		cursor: pointer;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.right{
		width:40%;
		height: 300px;
		float: left ;
		padding: 10px;
		background: #EBC7A7;
		cursor: pointer;
		.title{
			font-size:18px;
			color: #79242F;
			margin-bottom: 10px;
		}
		.text{
			font-size:15px;
			color: #333333;
			line-height: 30px;
			margin-bottom: 30px;
		}
		span{
			font-size: 15px;
			color: #79242F;
		}

	}
}
  .list{
	  margin-bottom: 20px;
  }
</style>


